.non-hamburger-menu {
  align-items: flex-start;
  display: flex;
  flex-wrap: nowrap; }

@media only screen and (max-width: 700px) {
  .non-hamburger-menu {
    align-items: flex-start;
    display: none;
    flex-wrap: nowrap; } }

.hamburger-menu {
  display: none;
  cursor: pointer;
  float: right; }

@media only screen and (max-width: 700px) {
  .hamburger-menu {
    display: block;
    cursor: pointer;
    float: right; } }

.bar1, .bar2, .bar3 {
  width: 32px;
  height: 4px;
  border-radius: 0.3rem;
  background-color: #efefef;
  margin: 6px 0 0 1rem;
  transition: 0.4s; }

.change .bar1 {
  transform: rotate(-45deg) translate(-6px, 7px); }

.change .bar2 {
  opacity: 0; }

.change .bar3 {
  transform: rotate(45deg) translate(-6px, -7px); }

.animate .nav-links {
  display: flex;
  transform: rotate(45deg) translate(-100px, 0px); }
